body {
    font-family: Arial, sans-serif;
    background-color:#ccc;
    image-resolution: "https://tse1.explicit.bing.net/th?id=OIP.JzWR2BgiZ0DoQE6NxuDdDQHaFk&pid=Api&P=0&h=180";
}

.container {
    max-width: 400px;
    margin: 100px auto;
    padding: 20px;
    background-color:aqua;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.form-group {
    margin-bottom: 20px;
}

label {
    display: block;
    margin-bottom: 5px;
}

input[type="text"],
input[type="password"],
button {
    width: 10%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
}
 
    .woo{
        margin: right 20px;;
    }


button {
    background-color:purple;
    color: #fff;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}